:root {
  --black: #000000;
  --ac-blue: #2b3f99;
  --ac-pink: #d21d52;
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* General Styles */

html {
  position: relative;
  min-height: 100%
}

body {
  background-image: url(../src/assets/images/hex.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  margin-bottom: 3.5rem;
  background-color: #000000;
}

body::-webkit-scrollbar {
  width: 0px;
  background-color: transparent;
}

.flex {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap
}

/* Header Styling */

.cartIcon {
  font-size: 1.5em;
  cursor: pointer;
  color: var(--ac-blue);
  transition: transform 0.2s ease-in-out;
}

.active {
  color: var(--clr-neon);
}

.shake {
  animation: shake 0.5s !important;
  animation: shake 0.5s ease-in-out;
  animation-fill-mode: forwards;
}

@keyframes shake {
  0% { transform: scale(1); }
  20% { transform: scale(1.2) rotate(5deg); }
  40% { transform: scale(1.2) rotate(-5deg); }
  60% { transform: scale(1.2) rotate(5deg); }
  80% { transform: scale(1.2) rotate(-5deg); }
  100% { transform: scale(1); }
}

li {
  list-style: none;
  font-size: 1.5em;
  cursor: pointer;
}

a {
  text-decoration: none;
}

.even {
  color: var(--ac-blue);
}
.odd {
  color: var(--ac-pink);
}

header,
ul {
  display: flex;
  justify-content: space-between;
  gap: 2em;
  
}

header {
  padding: 1.5em;
  background-color: black;
  align-items: center;
  color: #fff;
}

.title {
  color: var(--ac-pink);
  text-align: center;
}

/* Home Styling */

.overlay {
  background-color: rgba(0, 0, 0, .7);
  padding: 2em;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.gradient-text {
  background-image: linear-gradient(to right, #ef2562, #2c4de2, #ef2562);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  font-size: 5em;
  
} 

.home {
  padding: 12em 2em 3em 2em;
}

/* Shop Styling */

.cardCont {
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
  margin-right: auto;
  margin-left: auto;
}

/* Tile Styling */

.tile {
  color: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 2em;
  border-top: 1px solid var(--ac-blue);
  border-bottom: 1px solid var(--ac-pink);
  padding: 1em 0;
  font-size: 1.2em;
}

.tile-info {
  color: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 2em;
  margin: 1em 0;
}

.tileBtn {
  border: 2px solid var(--clr-neon);
  background-color: transparent;
  outline: none;
  padding: .5em;
  color: var(--clr-neon);
  cursor: pointer;
  margin-right: .2em;
}

/* ProductDetails */

.productDetails {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  color: #fff;
}

.productDetails > img {
  max-width: 450px;
  padding: 2em
}

.productInfo {
  font-size: 1.5em;
  max-width: 500px;
  
}